<template>
  <nav>
    <div class="nav-wrap green">
      <div class="container">
        <router-link to="/" class="brand-logo">Employee Manager</router-link>
        <ul class="right">
          <li v-if="isLoggedIn"><router-link to="/"><span class="black-text">{{currentUser}}</span></router-link></li>
          <li v-if="isLoggedIn"><router-link to="/" class="home">Dashboard</router-link></li>
          <li v-if="!isLoggedIn"><router-link to="/login">Login</router-link></li>
          <li v-if="!isLoggedIn"><router-link to="/register">Register</router-link></li>
          <li v-if="isLoggedIn"><button v-on:click="logout" class="btn black">Logout</button></li>
        </ul>
      </div>
    </div>
  </nav>
</template>

<script>
import firebase from 'firebase'
export default {
  name: 'navbar',
  date(){
    return{
      isLoggedIn: false,
      currentUser: false
    }
  },
  created(){
    if(firebase.auth().currentUser){
      this.isLoggedIn = true;
      this.currentUser = firebase.auth().currentUser.email;
    }
  },
  methods: {
    logout(){
      firebase.auth().signOut().then(() => {this.$router.go({ path: this.$router.path });})
    }
  }
}
</script>

<style scoped>
.home{margin-right: 20px;}
</style>



