<template>
  <div id="dashboard">
    <!-- Fetch The Employees Data -->
    <ul class="collection with-header">
      <li class="collection-header">
        <h5>Employees</h5>
      </li>
      <li v-for="employee in employees" :key="employee.id" class="collection-item">
        <div class="chip">{{employee.dept}}</div> {{employee.employee_id}} __ {{employee.name}}

        <router-link class="secondary-content" :to="{name: 'view-employee', params: {employee_id: employee.employee_id}}"><i class="fa fa-eye"></i></router-link>
      </li>
    </ul>

    <div class="fixed-action-btn">
      <router-link to="/new" class="btn-floating btn-large red">
        <i class="fa fa-plus"></i>
      </router-link>
    </div>
  </div>
</template>

<script>
import db from "./firebaseInit"
export default {
  name: 'dashboard',
  data(){
    return{
      employees: []
    }
  },
  created(){
    db.collection('employees').orderBy('dept').get().then(Query => {
      Query.forEach(doc => {
        const data = {
          'id': doc.id,
          'employee_id': doc.data().employee_id,
          'name': doc.data().name,
          'dept': doc.data().dept,
          'position': doc.data().position
        }
        this.employees.push(data)
      })
    })
  }
}
</script>



